.b-wrapper {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background: #fff;
}

.b-pullup-wrapper {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 60px;
    line-height: 60px;
}

.b-pulldown-wrapper {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: all;
    -o-transition: all;
    transition: all;
    pointer-events: none;
}

.b-pulldown-wrapper .after-trigger {
    margin-top: 10px
}



